
<div class="py-4">
  <vuetable-filter-bar />
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info
      ref="paginationInfoTop"
      info-template="Displaying {from} to {to} of {total} pages"
    />
    <vuetable-pagination
      ref="paginationTop"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
  <div class="overflow-x-auto overflow-y-hidden">
    <vuetable
      ref="vuetable"
      :api-url="apiUrl"
      :append-params="moreParams"
      :css="css"
      :fields="fields"
      :per-page="20"
      :sort-order="sortOrder"
      @vuetable:pagination-data="onPaginationData"
      @vuetable:row-clicked="onRowClicked"
      @vuetable:loaded="onLoaded"
    >
      <template
        slot="page-listing-display"
        slot-scope="props"
      >
        <page-result-cell
          :title="props.rowData.title"
          :url="props.rowData.url"
          :width="0"
          color="none"
        />
      </template>
      <template
        slot="sample-date"
        slot-scope="props"
      >
        <data-sample-date
          :date="props.rowData.latestErrorDate"
          :query="props.rowData.query"
          :url="props.rowData.url"
        />
      </template>
      <template
        slot="craft-errors"
        slot-scope="props"
      >
        <error-warning :sample="props.rowData.craftCount" />
      </template>
      <template
        slot="boomerang-errors"
        slot-scope="props"
      >
        <error-warning :sample="props.rowData.boomerangCount" />
      </template>
      <template
        slot="total-errors"
        slot-scope="props"
      >
        <error-warning :sample="props.rowData.cnt" />
      </template>
    </vuetable>
  </div>
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info
      ref="paginationInfo"
      info-template="Displaying {from} to {to} of {total} pages"
    />
    <vuetable-pagination
      ref="pagination"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
</div>
